import React, { useState } from "react"
import { login } from "../../services/functions/auth"
import ErrorBoundary from "../../components/ErrorBoundary"

import TextField from "@material-ui/core/TextField"
import Recaptcha from "../../components/UI/Recaptcha"
import { GoogleReCaptchaProvider } from "react-google-recaptcha-v3"
import recaptchaKey from "../../config/recaptcha"
import estilos from "./estilos.module.css"

const Login = (props) => {
  const { appState } = props

  const [usuario, setUsuario] = useState("teste")
  const [senha, setSenha] = useState("12345678")

  const loginHandler = (recaptchaToken) => {
    login({
      appState,
      recaptchaToken: recaptchaToken,
      senha: senha,
      usuario: usuario,
    })
  }

  return (
    <ErrorBoundary>
      <div className={estilos.Container}>
        <div className={estilos.Form}>
          <div className={estilos.Titulo}>HARDT Admin</div>
          <form onSubmit={(e) => e.preventDefault()}>
            <TextField
              disabled={appState.loading}
              fullWidth={true}
              label={"UsuÃ¡rio"}
              margin={"dense"}
              onChange={(event) => setUsuario(event.target.value)}
              type={"text"}
              value={usuario}
              variant={"filled"}
            />

            <TextField
              type={"password"}
              label={"Senha"}
              margin={"dense"}
              variant={"filled"}
              fullWidth={true}
              disabled={appState.loading}
              value={senha}
              onChange={(event) => setSenha(event.target.value)}
            />

            <div
              className={estilos.BtnLogin}
              onClick={() => appState.startLoading()}
            >
              <GoogleReCaptchaProvider reCaptchaKey={recaptchaKey}>
                <Recaptcha
                  action="Login"
                  exe={loginHandler}
                  loading={appState.loading}
                />
              </GoogleReCaptchaProvider>
            </div>
          </form>
        </div>
      </div>
    </ErrorBoundary>
  )
}

export default Login
